#side > div {
  display: block;
}

#side > div > input + label {
  cursor: pointer;
  display: block;
}

#side > div > input + label:hover {
  cursor: pointer;
  display: block;
  color:lightgrey;
}

#side > div > input:checked + label {
  cursor: pointer;
  display: block;
  color:lightgrey;
  font-weight: bold;
}

.subLinks {
  visibility: visible;
  display: block;
  opacity: 1;
}